import { forwardRef } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Progress } from '@/components/ui/progress';
import { BarChart, Bar, XAxis, YAxis, Tooltip, AreaChart, Area, PieChart, Pie, Cell, ResponsiveContainer } from 'recharts';
import { Users, CheckCircle, Bug, Calendar, TrendingUp } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';

// Dummy Data
const dummyData = {
  teams: [
    { name: 'Frontend', completedTasks: 75, totalTasks: 100 },
    { name: 'Backend', completedTasks: 60, totalTasks: 80 },
  ],
  tasksOverTime: [
    { date: '2025-09-01', completed: 20, inProgress: 10, pending: 20 },
    { date: '2025-09-15', completed: 30, inProgress: 5, pending: 10 },
  ],
  subtasks: [
    { name: 'Completed', value: 60 },
    { name: 'Pending', value: 40 },
  ],
  bugs: [
    { name: 'Open', value: 10 },
    { name: 'Resolved', value: 15 },
    { name: 'Critical', value: 3 },
  ],
  summary: {
    totalTeams: 2,
    totalTasks: 180,
    totalSubtasks: 100,
    bugs: { open: 10, resolved: 15, critical: 3 },
    completedMoms: 3,
  },
};

// Colors for charts
const COLORS = {
  completed: '#22c55e',
  inProgress: '#facc15',
  pending: '#ef4444',
  open: '#3b82f6',
  resolved: '#22c55e',
  critical: '#ef4444',
};

const ProjectReport = forwardRef(({ dateRange }, ref) => {
  // You can filter data based on dateRange if needed, but for dummy, ignore

  const renderSummaryCards = () => (
    <motion.div
      className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4 mb-6"
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5 }}
    >
      <Card>
        <CardHeader className="flex items-center gap-2">
          <Users className="h-5 w-5" />
          <CardTitle>Teams</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-xl font-bold">{dummyData.summary.totalTeams}</p>
        </CardContent>
      </Card>
      <Card>
        <CardHeader className="flex items-center gap-2">
          <CheckCircle className="h-5 w-5" />
          <CardTitle>Tasks</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-xl font-bold">{dummyData.summary.totalTasks}</p>
          <Progress value={(dummyData.teams.reduce((sum, t) => sum + t.completedTasks, 0) / dummyData.summary.totalTasks) * 100} className="mt-2" />
        </CardContent>
      </Card>
      <Card>
        <CardHeader className="flex items-center gap-2">
          <Bug className="h-5 w-5" />
          <CardTitle>Bugs</CardTitle>
        </CardHeader>
        <CardContent>
          <p>Open: <Badge variant="destructive">{dummyData.summary.bugs.open}</Badge></p>
          <p>Critical: <Badge variant="destructive">{dummyData.summary.bugs.critical}</Badge></p>
        </CardContent>
      </Card>
      <Card>
        <CardHeader className="flex items-center gap-2">
          <Calendar className="h-5 w-5" />
          <CardTitle>MOMs</CardTitle>
        </CardHeader>
        <CardContent>
          <p className="text-xl font-bold">{dummyData.summary.completedMoms}</p>
        </CardContent>
      </Card>
    </motion.div>
  );

  const renderTeamProgress = () => (
    <motion.div
      initial={{ opacity: 0, scale: 0.95 }}
      animate={{ opacity: 1, scale: 1 }}
      transition={{ duration: 0.5 }}
    >
      <Card className="mb-6">
        <CardHeader className="flex items-center gap-2">
          <TrendingUp className="h-5 w-5" />
          <CardTitle>Team Progress</CardTitle>
        </CardHeader>
        <CardContent>
          <ResponsiveContainer width="100%" height={200}>
            <BarChart data={dummyData.teams}>
              <XAxis dataKey="name" />
              <YAxis />
              <Tooltip formatter={(value) => `${value}%`} />
              <Bar dataKey="completedTasks" fill={COLORS.completed} maxBarSize={40}>
                {dummyData.teams.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={COLORS.completed} />
                ))}
              </Bar>
            </BarChart>
          </ResponsiveContainer>
        </CardContent>
      </Card>
    </motion.div>
  );

  const renderTasksSubtasks = () => (
    <motion.div
      className="grid grid-cols-1 sm:grid-cols-2 gap-4 mb-6"
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ duration: 0.5, delay: 0.2 }}
    >
      <Card>
        <CardHeader className="flex items-center gap-2">
          <CheckCircle className="h-5 w-5" />
          <CardTitle>Tasks</CardTitle>
        </CardHeader>
        <CardContent>
          <ResponsiveContainer width="100%" height={200}>
            <AreaChart data={dummyData.tasksOverTime}>
              <XAxis dataKey="date" />
              <YAxis />
              <Tooltip />
              <Area type="monotone" dataKey="completed" stackId="1" fill={COLORS.completed} stroke={COLORS.completed} />
              <Area type="monotone" dataKey="inProgress" stackId="1" fill={COLORS.inProgress} stroke={COLORS.inProgress} />
              <Area type="monotone" dataKey="pending" stackId="1" fill={COLORS.pending} stroke={COLORS.pending} />
            </AreaChart>
          </ResponsiveContainer>
        </CardContent>
      </Card>
      <Card>
        <CardHeader className="flex items-center gap-2">
          <CheckCircle className="h-5 w-5" />
          <CardTitle>Subtasks</CardTitle>
        </CardHeader>
        <CardContent>
          <ResponsiveContainer width="100%" height={200}>
            <PieChart>
              <Pie data={dummyData.subtasks} dataKey="value" nameKey="name" cx="50%" cy="50%" outerRadius={80} label>
                {dummyData.subtasks.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={entry.name === 'Completed' ? COLORS.completed : COLORS.pending} />
                ))}
              </Pie>
              <Tooltip />
            </PieChart>
          </ResponsiveContainer>
        </CardContent>
      </Card>
    </motion.div>
  );

  const renderBugStatus = () => (
    <motion.div
      initial={{ opacity: 0, scale: 0.95 }}
      animate={{ opacity: 1, scale: 1 }}
      transition={{ duration: 0.5, delay: 0.3 }}
    >
      <Card className="mb-6">
        <CardHeader className="flex items-center gap-2">
          <Bug className="h-5 w-5" />
          <CardTitle>Bug Status</CardTitle>
        </CardHeader>
        <CardContent>
          <ResponsiveContainer width="100%" height={200}>
            <PieChart>
              <Pie data={dummyData.bugs} dataKey="value" nameKey="name" cx="50%" cy="50%" innerRadius={40} outerRadius={80} label>
                {dummyData.bugs.map((entry, index) => (
                  <Cell key={`cell-${index}`} fill={COLORS[entry.name.toLowerCase()]} />
                ))}
              </Pie>
              <Tooltip />
            </PieChart>
          </ResponsiveContainer>
        </CardContent>
      </Card>
    </motion.div>
  );

  return (
    <div ref={ref}>
      <AnimatePresence>
        {renderSummaryCards()}
        {renderTeamProgress()}
        {renderTasksSubtasks()}
        {renderBugStatus()}
      </AnimatePresence>
    </div>
  );
});

ProjectReport.displayName = 'ProjectReport';

export default ProjectReport;