import { forwardRef } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Progress } from '@/components/ui/progress';
import { AreaChart, Area, XAxis, YAxis, Tooltip, ResponsiveContainer } from 'recharts';
import { CheckCircle, TrendingUp } from 'lucide-react';
import { motion } from 'framer-motion';

// Dummy Data for Employee Performance
const employeeData = {
  tasksOverTime: [
    { date: '2025-09-01', completed: 5, inProgress: 3, pending: 2 },
    { date: '2025-09-15', completed: 8, inProgress: 2, pending: 1 },
  ],
  performance: {
    completionRate: 80,
    tasksCompleted: 13,
    subtasksCompleted: 25,
  },
};

// Colors
const COLORS = {
  completed: '#22c55e',
  inProgress: '#facc15',
  pending: '#ef4444',
};

const Performance = forwardRef(({ dateRange }, ref) => {
  // You can filter based on dateRange if needed

  return (
    <div ref={ref}>
      <motion.div
        className="grid grid-cols-1 sm:grid-cols-3 gap-4 mb-6"
        initial={{ opacity: 0, y: 20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.5 }}
      >
        <Card>
          <CardHeader className="flex items-center gap-2">
            <CheckCircle className="h-5 w-5" />
            <CardTitle>Completion</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-xl font-bold">{employeeData.performance.completionRate}%</p>
            <Progress value={employeeData.performance.completionRate} className="mt-2" />
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex items-center gap-2">
            <CheckCircle className="h-5 w-5" />
            <CardTitle>Tasks</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-xl font-bold">{employeeData.performance.tasksCompleted}</p>
          </CardContent>
        </Card>
        <Card>
          <CardHeader className="flex items-center gap-2">
            <CheckCircle className="h-5 w-5" />
            <CardTitle>Subtasks</CardTitle>
          </CardHeader>
          <CardContent>
            <p className="text-xl font-bold">{employeeData.performance.subtasksCompleted}</p>
          </CardContent>
        </Card>
      </motion.div>

      <motion.div
        initial={{ opacity: 0, scale: 0.95 }}
        animate={{ opacity: 1, scale: 1 }}
        transition={{ duration: 0.5 }}
      >
        <Card>
          <CardHeader className="flex items-center gap-2">
            <TrendingUp className="h-5 w-5" />
            <CardTitle>Task Trends</CardTitle>
          </CardHeader>
          <CardContent>
            <ResponsiveContainer width="100%" height={200}>
              <AreaChart data={employeeData.tasksOverTime}>
                <XAxis dataKey="date" />
                <YAxis />
                <Tooltip />
                <Area type="monotone" dataKey="completed" stackId="1" fill={COLORS.completed} stroke={COLORS.completed} />
                <Area type="monotone" dataKey="inProgress" stackId="1" fill={COLORS.inProgress} stroke={COLORS.inProgress} />
                <Area type="monotone" dataKey="pending" stackId="1" fill={COLORS.pending} stroke={COLORS.pending} />
              </AreaChart>
            </ResponsiveContainer>
          </CardContent>
        </Card>
      </motion.div>
    </div>
  );
});

Performance.displayName = 'Performance';

export default Performance;